@charset "UTF-8";
@font-face {
  font-family: 'Gerbera';
  src: url("../fonts/Gerbera-Light.eot?#iefix") format("embedded-opentype"), url("../fonts/Gerbera-Light.woff2") format("woff2"), url("../fonts/Gerbera-Light.woff") format("woff"), url("../fonts/Gerbera-Light.ttf") format("truetype");
  font-weight: 300;
  font-style: normal; }

@font-face {
  font-family: 'Gerbera';
  src: url("../fonts/Gerbera-Medium.eot?#iefix") format("embedded-opentype"), url("../fonts/Gerbera-Medium.woff2") format("woff2"), url("../fonts/Gerbera-Medium.woff") format("woff"), url("../fonts/Gerbera-Medium.ttf") format("truetype");
  font-weight: 500;
  font-style: normal; }

html,
body {
  background: #3a91f5;
  width: 100%;
  min-height: 100vh;
  padding: 0;
  margin: 0; }

body {
  font-family: "Gerbera", sans-serif;
  font-size: 16px;
  line-height: 1.3em;
  background-image: url(../img/background-bg.svg);
  background-repeat: no-repeat;
  background-position: center top;
  background-size: cover; }
  @media screen and (min-width: 1200px) {
    body {
      background-position: 0 0; } }

img {
  max-width: 100%; }

h1 {
  font-family: "Gerbera", sans-serif;
  font-size: 44px;
  line-height: 1.3em;
  font-weight: 500;
  padding: 0;
  margin: 0; }

h3 {
  font-family: "Gerbera", sans-serif;
  font-size: 20px;
  line-height: 1.3em;
  font-weight: 500;
  padding: 0;
  margin: 0; }

.btn {
  display: block;
  font-family: "Gerbera", sans-serif;
  font-size: 16px;
  line-height: 1;
  font-weight: 500;
  text-align: center;
  text-decoration: none;
  border-radius: 100px;
  padding: 17px;
  cursor: pointer;
  color: #fff;
  background: #f45544; }
  @media screen and (min-width: 1200px) {
    .btn {
      padding: 17px 30px;
      display: inline-block; } }
  .btn:hover {
    opacity: 0.95; }

.mobile-hidden {
  display: none; }
  @media screen and (min-width: 1200px) {
    .mobile-hidden {
      display: block; } }

@media screen and (min-width: 1200px) {
  .row {
    display: flex; } }

.container {
  min-width: 280px;
  max-width: 1120px;
  padding: 0 20px;
  margin: 0 auto; }
  @media screen and (min-width: 1200px) {
    .container {
      padding: 0 30px; } }

.header {
  padding: 60px 0 45px; }
  .header__h1 {
    color: #fff; }

.content {
  padding-top: 30px; }
  @media screen and (min-width: 1200px) {
    .content {
      padding-top: 0; } }

.leftblock {
  color: #fff;
  font-weight: 500; }
  @media screen and (min-width: 1200px) {
    .leftblock {
      width: 23%; } }
  .leftblock__number {
    font-size: 36px;
    margin-right: 2px; }
  .leftblock__description {
    margin: 0 0 20px 0; }
    @media screen and (min-width: 1200px) {
      .leftblock__description {
        margin: 0 0 30px 0; } }
    .leftblock__description_left {
      padding-left: 20px; }
  .leftblock__mobile {
    display: flex; }
    @media screen and (min-width: 1200px) {
      .leftblock__mobile {
        display: block; } }
    .leftblock__mobile-left {
      margin-right: 20px; }
      @media screen and (min-width: 1200px) {
        .leftblock__mobile-left {
          display: none; } }

@media screen and (min-width: 1200px) {
  .middleblock {
    width: 47%; } }

.bubble {
  background: #fff;
  box-shadow: 0px 2px 25px rgba(0, 0, 0, 0.1);
  border-radius: 24px;
  position: relative;
  font-weight: 500;
  min-height: 220px; }
  @media screen and (min-width: 1200px) {
    .bubble {
      margin: 0 37px 0 10px;
      min-height: 250px;
      border-radius: 34px; } }
  .bubble::after {
    display: table;
    clear: both; }
  .bubble__tail {
    width: 0;
    height: 0;
    border-left: 0px solid transparent;
    border-right: 18px solid transparent;
    border-top: 18px solid #fff;
    position: absolute;
    right: auto;
    left: 50px;
    bottom: -18px; }
    @media screen and (min-width: 1200px) {
      .bubble__tail {
        width: 0;
        height: 0;
        border-left: 18px solid transparent;
        border-right: 0px solid transparent;
        border-top: 18px solid #fff;
        left: auto;
        right: 50px; } }
  .bubble__img {
    float: right;
    margin: -10px -10px 10px 20px; }
  .bubble__ttl {
    letter-spacing: -0.5px;
    color: #a84fd4;
    margin: 5px 0 22px;
    animation: bubbleAnimation 2s infinite; }
  .bubble__name {
    display: flex;
    align-items: center;
    animation: bubbleAnimation 2s infinite; }
    .bubble__name-txt {
      padding-left: 10px;
      letter-spacing: -0.3px;
      color: #0b276f;
      font-weight: 300; }
      @media screen and (min-width: 1200px) {
        .bubble__name-txt {
          color: #272727;
          font-weight: 500; } }
  .bubble__txt {
    font-weight: 300;
    line-height: 1.4em;
    margin: 8px 0 0 0;
    color: #272727;
    animation: bubbleAnimation 2s infinite; }
    @media screen and (min-width: 1200px) {
      .bubble__txt {
        margin: 20px 0 0 0; } }
  .bubble__slide {
    position: absolute;
    top: 0;
    width: calc(100% - 56px);
    padding: 15px 18px;
    transition: opacity 0.2s ease-out;
    opacity: 0; }
    @media screen and (min-width: 1200px) {
      .bubble__slide {
        padding: 28px 28px 32px 28px; } }
    .bubble__slide_active {
      opacity: 1; }

.rightblock {
  color: #fff;
  font-weight: 500;
  position: relative; }
  @media screen and (min-width: 1200px) {
    .rightblock {
      width: 30%; } }
  .rightblock__info {
    margin: 0 0 20px 0; }
    @media screen and (min-width: 1200px) {
      .rightblock__info {
        margin: 20px 0; } }
  .rightblock__round-pic1 {
    margin: 20px 0 0 0; }
    @media screen and (min-width: 1200px) {
      .rightblock__round-pic1 {
        position: absolute;
        top: -150px;
        left: -15px;
        margin: 0; } }
  .rightblock__round-pic2 {
    position: absolute;
    top: -175px;
    right: 0; }
  .rightblock__round-pic3 {
    margin: 35px 0 0 15px; }

/* keyframes для анимации бабла */
@keyframes bubbleAnimation {
  0%, 60%, 75%, 90%, to {
    animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1); }
  0% {
    opacity: 0;
    transform: translate3d(10px, 0, 0); }
  60% {
    opacity: 1;
    transform: translate3d(-5px, 0, 0); }
  75% {
    transform: translate3d(10px, 0, 0); }
  90% {
    transform: translate3d(-5px, 0, 0); }
  to {
    transform: translateZ(0); } }
